/*明星单品*/
#starproduct {
	width: 1226px;
	margin: auto;
}
#starBox {
  width: 1226px;
  height: 380px;
}
.staritem {
  position: relative;
  width: 238px;
	height: 350px;
  margin-right: 9px;
  margin-top: 15px; 
	background-color: pink;
  border-top: grey solid 2px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 15);
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  float: left;
}
.staritem img{
  width: 100%;
  height:100%;
}
.staritem::after {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 15);
  opacity: 0;
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.staritem:hover {
  -webkit-transform: scale(1.05, 1.05);
          transform: scale(1.05, 1.05);
}
.staritem:hover::after {
  opacity: 1;
}

.staritem:nth-of-type(5n+0) {
	margin-right: 0px;
}


